        },
      ],
      isClick:false
    };
  },
  methods: {
    changeBtn(){
      this.isClick = !this.isClick;
    },
    isGit(n){
      if(n){
        this.isClick = false;
      }else{
        this.isClick = true;
      }
    },
    isChirGit(){
      this.isClick = false;
    }
  },
};
</script>

