import Vue from 'vue'
import VueRouter from 'vue-router'
import routes from './routes'
// if (!window.VueRouter) {
//    
// }
Vue.use(VueRouter)



const router = new VueRouter({
    mode: 'hash',
    routes
})
router.afterEach((to) => {
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
    if (to.meta.title == '首页') {
        document.title = '英雄联盟手游官网 - 腾讯游戏';
    } else {
        document.title = to.meta.title + ' - 英雄联盟手游官网 - 腾讯游戏';
    }
})
export default router